<template>
  <div class="outline-wrap">
    <div class="head">{{optName}}</div>
    <el-form class="create-form" ref="form" :model="form" :rules="rules" label-width="100px">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-form-item label="大纲名称：">
            <el-input v-model="form.Name" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="大纲学期：" prop="TermID">
            <el-select v-model="form.TermID" placeholder="请选择">
              <el-option
                v-for="(item,index) in terms"
                :key="index"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="课程名称：" prop="CourseID">
            <el-select v-model="form.CourseID" placeholder="请选择">
              <el-option
                v-for="(item,index) in courses"
                :key="index"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="教学目标：">
        <summer-note :eqIndex="0"></summer-note>
      </el-form-item>
      <el-form-item label="教学方式：">
        <summer-note :eqIndex="1"></summer-note>
      </el-form-item>
      <el-form-item label="教学安排及方式：">
        <summer-note :eqIndex="2"></summer-note>
      </el-form-item>
      <el-form-item label="考核方式">
        <summer-note :eqIndex="3"></summer-note>
      </el-form-item>
      <el-form-item label="推荐教材与参考：">
        <summer-note :eqIndex="4"></summer-note>
      </el-form-item>
      <el-form-item>
        <div class="btns">
          <el-button size="medium" type="primary" plain @click="onSubmit">保存</el-button>
          <el-button size="medium" type="info" plain>取消</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import summerNote from "../../../../components/personal/summernote.vue";
//import { isError } from "util";
export default {
  components: {
    summerNote
  },
  data() {
    // let validateName = async (rule, value, callback) => {
    //   const res = await http.post(api.validateTeachingProgramName, {
    //     id: this.TPID,
    //     name: value
    //   });
    //   if (res.type) {
    //     callback(new Error(res.message));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      treeUpdata: false,
      optName: "",
      isEdit: "",
      tpid: "",
      terms: [],
      courses: [],
      form: {
        TermID: "",
        CourseID: "",
        Name: "",
        TPgoal: "",
        TPMethods: "",
        TPArrange: "",
        TPAssessment: "",
        TPRecommend: ""
      },
      rules: {
        Name: [
          {
            required: true,
            min: 2,
            max: 25,
            message: "长度在 2 到 25 个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.judegeSta();
  },
  methods: {
    judegeSta() {
      this.tpid = this.$route.query.tpid;
      if (this.tpid == "" || this.tpid == undefined) this.isEdit = false;
      else this.isEdit = true;
      if (this.isEdit) this.optName = "修改大纲";
      else this.optName = "创建大纲";
      this.initData();
    },
    initData: async function() {
      const res = await http.get(api.getTermsBySchool, {
        sid: "",
        Token: http.getToken()
      });
      if (res.Result) {
        res.Datas.forEach(element => {
          this.terms.push({
            value: element.TermID,
            label: element.TermName
          });
        });
      }
      const res1 = await http.get(api.getAllCourseDatas, {
        Token: http.getToken()
      });
      if (res1.Result) {
        res1.Datas.forEach(element => {
          this.courses.push({
            value: element.CourseID,
            label: element.CourseName
          });
        });
      }
      if (!this.isEdit) {
        this.$set(this.form, "Name", "");
        this.$set(this.form, "TermID", "");
        this.$set(this.form, "CourseID", "");
        $(".summernote")
          .eq(0)
          .summernote("code", "");
        $(".summernote")
          .eq(1)
          .summernote("code", "");
        $(".summernote")
          .eq(2)
          .summernote("code", "");
        $(".summernote")
          .eq(3)
          .summernote("code", "");
        $(".summernote")
          .eq(4)
          .summernote("code", "");
      } else {
        const res2 = await http.get(api.getTeachingProgramData, {
          id: this.tpid,
          Token: http.getToken()
        });
        if (res2.Result) {
          if (res2.Data == null) return;
          this.form.Name = res2.Data.TPName;
          this.form.TermID = res2.Data.TermID;
          this.form.CourseID = res2.Data.CourseID;
          $(".summernote")
            .eq(0)
            .summernote("code", res2.Data.TPgoal);
          $(".summernote")
            .eq(1)
            .summernote("code", res2.Data.TPMethods);
          $(".summernote")
            .eq(2)
            .summernote("code", res2.Data.TPArrange);
          $(".summernote")
            .eq(3)
            .summernote("code", res2.Data.TPAssessment);
          $(".summernote")
            .eq(4)
            .summernote("code", res2.Data.TPRecommend);
        }
      }
    },
    onSubmit() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          let params = {
            tpid: this.tpid,
            termid: this.form.TermID,
            courseid: this.form.CourseID,
            tpname: this.form.Name,
            tpgoal: $(".summernote")
              .eq(0)
              .summernote("code"),
            tpmethods: $(".summernote")
              .eq(1)
              .summernote("code"),
            tparrange: $(".summernote")
              .eq(2)
              .summernote("code"),
            tpassessment: $(".summernote")
              .eq(3)
              .summernote("code"),
            tprecommend: $(".summernote")
              .eq(4)
              .summernote("code"),
            Token: http.getToken()
          };
          this.saveData(params);
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    saveData: async function(params) {
      const res = await http.post(api.saveTeachingProgram, params);
      if (res.type) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "保存大纲成功",
          type: "success"
        });
        if (!this.isEdit) {
          this.tpid = res.Data;
          this.$parent.$refs.nav.updata = true;
        }
        this.$router.push({
          name: "programView",
          query: { tpid: this.tpid }
        });
      }
    }
  },
  watch: {
    $route(to, from) {
      this.judegeSta();
    }
  }
};
</script>
<style lang="less" scoped>
</style>



